#log-in {
  display: flex;
  justify-content: center;
  align-items: center;
}

#log-in-form input[type='password']{
  outline: none;
  margin-bottom: 5px;
  border: none;
  border-bottom: 2px solid black;
  opacity: 0.5;
  transition: width 0.4s ease-in-out;
  width: 100px;
  text-align: center;
}

#log-in-form input[type='password']:focus {
  opacity: 1;
  width:200px;
}

#log-in-form input[type='submit'] {
  border: none;
  color: black;
  background-color: white;
  opacity: 0.5;
}

#log-in-form input[type='submit']:hover {
  opacity: 1;
  cursor: pointer;
}
